import find from 'lodash/find'
import isMatch from 'lodash/isMatch'
import { useCallback, useEffect } from 'react'
import PropTypes from 'prop-types'

import github from './api'
import ValidationErrors from './ValidationErrors'
import { usePickerStore } from './store'

import { Control, Button, Select } from '../Controls'
import Spinner from '../../Common/Spinner'
import SuggestRemoteRepo from '../../Dialogs/SuggestRemoteRepo'
import SuggestRepoSelection from '../../Dialogs/SuggestRepoSelection'
import SuggestWrongRepo from '../../Dialogs/SuggestWrongRepo'
import LayoutGenerationError from '../../Dialogs/LayoutGenerationError'
import Blocked from '../../Dialogs/Blocked'

import Login from './Dialogs/Login'
import Install from './Dialogs/Install'
import Icon from '../../Common/Icon'

const isLayoutGenerationFailure = err => isMatch(err, { name: 'LayoutGenerationError' })
const isMissingConfig = err => isMatch(err, { name: 'MissingRepoFile', path: 'config' })
const isMissingLayout = err => isMatch(err, { name: 'MissingRepoFile', path: 'config/info.json' })
const isMissingBoard = err => (
  err?.name === 'MissingRepoFile' &&
  err?.path.match(/config\/boards\/[^/]+\/[^/]+\/[^/]+?\..*$/)
)

function GithubPicker (props) {
  const [state, actions] = usePickerStore()

  const { initialized, branches, selectedRepoId, selectedBranchName } = state
  const { keyboards, selectedKeyboard } = state
  const { loadingBranches, loadingKeyboard, loadError, loadWarnings } = state

  const { onSelect } = props

  useEffect(() => {
    github.init().then(actions.integrationInitialized)
    github.on('authentication-failed', () => github.beginLoginFlow())
    github.on('blocked', actions.integrationBlocked)
    github.on('repo-validation-error', error => actions.integrationError({ error }))
    github.on('layout-generation-failed', error => actions.integrationError({ error }))
  }, [actions])

  const loadBranches = useCallback(async function (repoId) {
    actions.loadingBranchesList()

    const repository = find(github.repositories, { id: repoId })
    const branches = await github.fetchRepoBranches(repository)

    actions.loadedBranchesList({ branches })
  }, [actions])

  const loadKeyboard = useCallback(async function (repoId, branch, keyboard) {
    const available = github.repositories
    const repository = find(available, { id: repoId })?.full_name

    actions.keyboardLoading()

    const response = await github.fetchLayoutAndKeymap(repository, branch, {
      keyboard
    })

    actions.keyboardLoadedSuccess({ response })
    actions.lintKeyboard(response)

    onSelect({ github: { repository, branch, keyboard }, ...response })
  }, [actions, onSelect])

  useEffect(() => {
    if (!selectedRepoId) {
      return
    }

    loadBranches(selectedRepoId)
  }, [selectedRepoId, loadBranches])

  useEffect(() => {
    if (!selectedRepoId || !selectedBranchName) {
      return
    }

    loadKeyboard(selectedRepoId, selectedBranchName, selectedKeyboard)
  }, [selectedRepoId, selectedBranchName, selectedKeyboard, loadKeyboard])

  if (!initialized) return null
  if (state.blocked) return <Blocked />
  if (!github.isGitHubAuthorized()) return <Login />
  if (!github.isAppInstalled()) return <Install />
  if (github.isAppSuspendedForRepoSelection()) {
    return <SuggestRepoSelection installation={github.installations[0]} />
  }

  const repositories = github.repositories.map(repo => ({
    id: repo.id,
    name: repo.name,
    full: repo.full_name
  }))

  const extras = github.installations.map(installation => ({
    id: installation.id,
    type: 'installation',
    name: `[Manage available repos for ${installation.account.login}...]`
  }))

  const repositoryChoices = [...repositories, ...extras]
  const branchChoices = branches.map(branch => ({ id: branch.name, name: branch.name }))
  const keyboardChoices = keyboards.map(keyboard => ({ id: keyboard, name: keyboard }))

  function handleSelectRepo ({ id, type }) {
    if (type === 'installation') {
      github.manageInstallationRepos(id)
      return
    }

    const repository = find(repositories, { id })
    actions.selectedRepository({ repository })
  }

  if (isLayoutGenerationFailure(loadError)) return <LayoutGenerationError onDismiss={actions.clearSelection} />
  if (isMissingBoard(loadError)) return <SuggestRemoteRepo onDismiss={actions.clearSelection} />
  if (isMissingConfig(loadError)) {
    return (
      <SuggestWrongRepo
        onDismiss={actions.clearSelection}
        otherRepoOrBranchAvailable={
          repositoryChoices.length > 1 ||
          branchChoices.length > 0
        }
      />
    )
  }

  // This is getting unwieldy and I'm sorry
  if (loadError && (
    !isLayoutGenerationFailure(loadError) &&
    !isMissingBoard(loadError) &&
    !isMissingLayout(loadError) &&
    !isMissingConfig(loadError)
  )) {
    return (
      <ValidationErrors
        title={loadError.name}
        errors={loadError.errors}
        otherRepoOrBranchAvailable={
          repositoryChoices.length > 1 ||
          branchChoices.length > 0
        }
        onDismiss={actions.clearSelection}
      />
    )
  }

  const selectedRepo = find(repositories, { id: selectedRepoId })

  return (
    <>
      <Select
        label="Repository"
        value={selectedRepo}
        options={repositoryChoices}
        onChange={handleSelectRepo}
        getValue={option => option.id}
        getOptionValue={option => option.id}
        getOptionLabel={option => option.full || option.name}
        formatOptionLabel={(option, { context }) => (
          <>{context === 'value' ? option.name : option.full || option.name}</>
        )}
      />

      {branches.length > 1 && (
        <Select
          label="Branch"
          value={selectedBranchName}
          options={branchChoices}
          onChange={({ id }) => actions.selectedBranch({ name: id })}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
        />
      )}

      {keyboards.length > 1 && (
        <Select
          label="Keyboard"
          value={selectedKeyboard}
          options={keyboardChoices}
          getOptionValue={option => option.id}
          getOptionLabel={option => option.name}
          onChange={({ id }) => actions.selectedKeyboard({ name: id })}
        />
      )}

      {(loadingBranches || loadingKeyboard) && (
        <Control>
          <Spinner />
        </Control>
      )}

      {loadWarnings && (
        <ValidationErrors
          title="Warning"
          errors={loadWarnings}
          onDismiss={actions.clearWarnings}
        />
      )}

      {selectedBranchName && !loadingKeyboard && (
        <Button onClick={loadKeyboard}>
          <Icon name="sync" />
        </Button>
      )}
    </>
  )
}

GithubPicker.propTypes = {
  onSelect: PropTypes.func.isRequired
}

export default GithubPicker
