import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Link from '../Common/Link'
import Modal from '../Common/Modal'

function LayoutGenerationError (props) {
  const { onDismiss } = props
  const issuesLink = (
    <Link external href="https://github.com/nickcoutsos/keymap-editor/issues">
      Keymap Editor Issues
    </Link>
  )
  return (
    <Modal>
      <DialogBox onDismiss={onDismiss}>
        <h2>Automatic Layout Generation Failed</h2>
        <p>
          No accompanying layout was found with your keymap file and the Keymap
          Editor was unable to generate one automatically for you.
        </p>
        <p>
          Something went wrong with the attempted automatic layout generation
          for your keyboard and there's no automatic way to recover, but if you
          want to reach out for assistance maybe we can work together to resolve
          any technical issues or else craft a manual layout for your keyboard.
        </p>
        <p>
          Share your experience at {issuesLink}.
        </p>
      </DialogBox>
    </Modal>
  )
}

LayoutGenerationError.propTypes = {
  onDismiss: PropTypes.func.isRequired
}

export default LayoutGenerationError
