import DialogBox from '../Common/DialogBox'
import Link from '../Common/Link'
import Modal from '../Common/Modal'

const wikiUrl = 'https://github.com/nickcoutsos/keymap-editor/wiki/Defining-keyboard-layouts'
const layoutToolsUrl = 'https://nickcoutsos.github.io/keymap-layout-tools/'

function SuggestAutoLayoutTweak (props) {
  const wikiLink = (
    <Link href={wikiUrl} external={true}>
      Keymap Editor Wiki: Defining keyboard layouts
    </Link>
  )

  const layoutToolsLink = (
    <Link href={layoutToolsUrl} external={true}>
      Keymap Layout Tools - Helper
    </Link>
  )

  return (
    <Modal>
      <DialogBox {...props}>
        <h2>This layout was Auto-Generated</h2>
        <p>
          Hi there! There wasn't a layout definition to accompany your keymap
          file, so the keymap editor tried to generate one automatically in a
          last-ditch effort to help you get into the app.
        </p>
        <p>
          This layout definition was (or will be) saved when you first save
          keymap changes with the keymap editor, so remember that if there are
          any tweaks you'd like to make to improve it you can do so by editing
          the <code>info.json</code> file. If you want an easier way to test
          changes try pasting your layout into the {layoutToolsLink} (work in
          progress).
        </p>
        <p>
          For more information on how to do that see {wikiLink}. If you run into
          any trouble, reach out by making an issue on the project repo and I'd
          be happy to discuss.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestAutoLayoutTweak.propTypes = {}

export default SuggestAutoLayoutTweak
