import find from 'lodash/find'
import PropTypes from 'prop-types'
import { useCallback, useEffect, useState } from 'react'

import LocalPicker from './Local/Picker'
import DemoPicker from './Demo/Picker'
import GithubPicker from './Github/Picker'
import { sourceChoices, defaultSource } from './sources'
import { Button, Select } from './Controls'
import { getErrorDialog } from './errors'
import SuggestAutoLayoutTweak from '../Dialogs/SuggestAutoLayoutTweak'
import Icon from '../Common/Icon'

function KeyboardPicker (props) {
  const { onSelect } = props
  const [source, setSource] = useState(defaultSource)
  const [{ autoGenerated }, setLayoutInfo] = useState({ autoGenerated: false })
  const [showDialog, setShowDialog] = useState(null)
  const closeDialog = useCallback(() => setShowDialog(null), [setShowDialog])
  const showLayoutHelp = useCallback(() => {
    setShowDialog({
      component: SuggestAutoLayoutTweak,
      onClose: closeDialog
    })
  }, [setShowDialog, closeDialog])

  const handleKeyboardSelected = useCallback(function (event) {
    const { warningFlags = [] } = event.keymap
    const { autoGenerated = false } = event.layoutMetadata || {}
    const error = getErrorDialog(warningFlags)

    if (error) {
      setShowDialog({ component: error, onClose: closeDialog })
      onSelect({ source, keyboard: null })
      return
    }

    setLayoutInfo({ autoGenerated })
    onSelect({ source, ...event })
  }, [onSelect, source, setShowDialog, closeDialog])

  useEffect(() => {
    localStorage.setItem('selectedSource', source)
  }, [source])

  // TODO: Use source to memoize a Picker as defined in './sources'
  // TODO: Turn this into a context? Can provide:
  // - renderable picker,
  // - renderable actions,
  // - callbacks to load/reload

  const selectedSource = find(sourceChoices, { id: source })

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'end',
      gap: '5px',
      margin: '5px'
    }}>
      <Select
        label="Source"
        value={selectedSource}
        options={sourceChoices}
        getOptionLabel={option => option.name}
        getOptionValue={option => option.id}
        onChange={({ id }) => setSource(id)}
      />

      {showDialog?.component && <showDialog.component onDismiss={showDialog.onClose} />}

      {source === 'local' && <LocalPicker onSelect={handleKeyboardSelected} />}
      {source === 'demo' && <DemoPicker onSelect={handleKeyboardSelected} />}
      {source === 'github' && <GithubPicker onSelect={handleKeyboardSelected} />}

      {autoGenerated && (
        <Button onClick={showLayoutHelp}>
          <Icon name="warning" /> Layout wrong?
        </Button>
      )}
    </div>
  )
}

KeyboardPicker.propTypes = {
  onSelect: PropTypes.func.isRequired
}

export default KeyboardPicker
